<template>
    <div id="app">
        <Header :numCorrect="numCorrect" :numTotal="numTotal" />

        <b-container class="bv-example-row">
            <b-row>
                <b-col sm="6" offset="3">
                    <QuestionBox v-if="questions.length" :currentQuestion="questions[index]" :prev="prev" :next="next" :increment="increment" />
                </b-col>
            </b-row>
        </b-container>
    </div>
</template>

<script>
    import Header from './components/Header.vue'
    import QuestionBox from './components/QuestionBox.vue'

    export default {
        name: 'app',
        components: {
            Header,
            QuestionBox
        },
        data() {
            return {
                questions: [],
                index: 0,
                numCorrect: 0,
                numTotal: 0
            }
        },
        methods: {
            next() {
                var msg = ""
                if (this.index < 9) {
                    this.index++
                }
                if (this.numTotal == 10) {
                    if (this.numCorrect < 5)
                        msg = "You are a noob quizzer"
                    else if (this.numCorrect >= 5 && this.numCorrect < 9)
                        msg = "You have what it takes to be a quizzer"
                    else
                        msg = "All heil the Pro Sports Quiz Master"
                    alert("Your score out of 10 is " + this.numCorrect + "\n" + msg)
                }
            },
            prev() {
                if (this.index > 0)
                    this.index--
            },
            increment(isCorrect) {
                if (isCorrect) {
                    this.numCorrect++
                }
                this.numTotal++
            }
        },
        mounted: function() {
            fetch('https://opentdb.com/api.php?amount=10&category=21&type=multiple', {
                    method: 'get'
                })
                .then((response) => {
                    return response.json()
                })
                .then((jsonData) => {
                    this.questions = jsonData.results
                })
        }
    }

</script>

<style>
    #app {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
        margin-top: 60px;
        background-color: blanchedalmond;
    }

</style>
